import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { HttpClient } from '@angular/common/http';
// import { ToastrService } from "ngx-toastr";
import { environment } from 'src/environments/environment';
@Injectable({
  providedIn: 'root'
})


export class ServiceService {
  constructor( private router: Router,
    private spinner: NgxSpinnerService, 
    // private toastr: ToastrService,
    private http: HttpClient) { }

  userDetails = new BehaviorSubject<Object>(undefined);
  checkEmailForForgotPage = new BehaviorSubject<String>('No Email');  //  No Email is the initial value.
  headerString = new BehaviorSubject<String>('');  //  '' is the initial value.
  pageNotfound = new BehaviorSubject<Boolean>(false);  //  '' is the initial value.
  spinnerProgressBar = new BehaviorSubject<Number>(0)
  policyBenefits = ''
  priceMatrix = ''
  accountSettings = ''
  productDetails = ''
  allgroupname = new BehaviorSubject<any[]>([]); //  '' is the initial value.

  spinnerStart() {
    this.spinner.show();
  }
  spinnerClose() {
    this.spinner.hide();
  }

  login(data){
    return this.http.post(environment.url + "/admin/loginAdmin",data)
  }

  addWard(data){
  return this.http.post(environment.url + "/admin/createAndUpdateWard",data)
  }

  addAllWard(){
    return this.http.get(environment.url + "/admin/getAllWards")
    }

    deleteWard(id){
      return this.http.post(environment.url + "/admin/deleteWardById",id)
    }


  
}

