

import { Component, HostListener } from '@angular/core';
import { ProgressBarMode } from '@angular/material/progress-bar';
import { NavigationStart, Router } from '@angular/router';
import { take } from 'rxjs/operators';
import { ServiceService } from './core/service.service';
@HostListener('window:resize', ['$event'])

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})

export class AppComponent {
  title = 'cdsmrai-hospital';
  screenwidth
  showHead = false
  showSideBar = false
  pagenotfoundd: Boolean = false
  mode: ProgressBarMode = 'indeterminate';
  value: any= 0;
  constructor(private router: Router,private service:ServiceService) {
    this.screenwidth = window.innerWidth
    if (this.screenwidth < 768) {
      this.showSideBar = false
    } 
    else {
      this.showSideBar = true
    }
    router.events.forEach((event) => {
      if (event instanceof NavigationStart) {
        this.pageNotFound()
        if (event['url'] == '/login' || event['url'] == '/otp' || event['url'].includes('/reset-password')
        || event['url'] == '/forgot-password' || event['url'] == '/' ||  event['url'] == '/pagenotfound' || event['url'] == '/shared/success') {
          this.showHead = false;
        } else {
          console.log(event['url']);
          
          this.showHead = true;
          window.scroll(0, 0)
          this.service.headerString.next(event['url'])
        }
      }
    });

    this.service.spinnerProgressBar.subscribe((val)=>{
      console.log(val);
      if(val > 0){
        this.mode = 'determinate'
        this.value = val
      }
    })
  }

  onResize(event) {
    this.screenwidth = event.target.innerWidth
    if (this.screenwidth < 768) {
      this.showSideBar = false
    }
    else {
      this.showSideBar = true
    }

  }

  pageNotFound(){
  //   this.service.pageNotfound
  //   .subscribe((val)=>{
  //     if(val){
  //       setTimeout(() => {        
  //         this.pagenotfoundd = true
  //       }, 10);
  //     }
  //     else{
  //       setTimeout(() => {        
  //         this.pagenotfoundd = false
  //       }, 10);
        
  //     }
  //   })
  }


}
